@import url(https://fonts.googleapis.com/css2?family=Karla:wght@400&display=swap);
html {
  font-size: 62.5%;
}

body {
  padding: 0;
  margin: 0;
  font-family: 'Karla', 'Roboto Condensed', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/golf-course-weather/static/media/golf-course-1.a9d27f8e.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-attachment: fixed;
  height: 100vh;
  width: 100vw;
  max-width: 100%;
  overflow-x: hidden;
}

code {
  font-family: 'Karla', 'Courier New',
    monospace;
}

@media (max-width: 800px) {
  body {
    background-size: cover;
  }
}

/* -apple-system, BlinkMacSystemFont */

/* source-code-pro, Menlo, Monaco, Consolas, */
.App {
  color: white;
  text-align: center;
  font-size: 50px;
}

.content {
  min-height: 100vh;
}

.attribution {
  height: 30px;
  margin-top: 30px;
  font-size: 8px;
}

.attribution > a {
  text-decoration: none;
}

.attribution > a:visited {
  color: #0000EE;
}
.Icon{width:50px}
.Frost{margin-right:1.2px}.Frost-icon{width:17px}.Frost-percent{vertical-align:top}
.WeatherCard{justify-content:center;align-items:center;min-width:100px;margin:15px 0;border:1px solid #fff;border-radius:5px;box-shadow:3px 3px rgba(0,0,0,.3);background:#fff;color:#000;font-size:13px}.WeatherCard-day{font-weight:600}.WeatherCard-icon{margin-top:-5px}.WeatherCard-temp{display:flex;justify-content:space-between;margin-top:-20px;padding:10px}.WeatherCard-raindrop{display:block;justify-content:center;margin-top:-8px;font-size:12px}.WeatherCard-raindrop>.icon{width:15px}.WeatherCard-raindrop>.pop{vertical-align:top}.WeatherCard-frost{display:block;justify-content:center;margin-top:-5px;font-size:12px}@media(max-width: 1000px){.WeatherCard{margin:2%}}@media(max-width: 350px){.WeatherCard{margin:1rem 0}}
