p;width:70%}}@media(max-width: 450px){.Course{margin:5rem 0}.Course-list{width:40%}.Course-weather-row{justify-content:center;flex-wrap:wrap;width:60%}}@media(max-width: 350px){.Course-list{flex-direction:row;justify-content:space-between;align-content:space-between;width:50%}.Course-weather-row{width:50%}}
