Weather-form{margin-top:0;margin-bottom:2%}.CourseWeather-search-bar{height:2.5rem}.CourseWeather-legend-card{margin:2rem 0}}@media(max-width: 499px){.CourseWeather-top-section{display:flex;flex-direction:column;justify-content:center;align-items:center}.CourseWeather-form{flex-direction:column}.CourseWeather-search-bar{width:60vw}.CourseWeather-search-btn{margin-left:0}}@media(max-width: 350px){.CourseWeather-header{font-size:4rem}}
